import {ADD_TODO, SET_VISIBILITY_FILTER, TOGGLE_TODO, Visibility_Filters} from "./actions";
import {combineReducers} from "redux";
import {REQUEST_MOVIES, REQUEST_MOVIES_FAILURE, REQUEST_MOVIES_SUCCESS} from "./movie-actions";

function visibilityFilter(state = Visibility_Filters.SHOW_ALL, action) {
    switch (action.type) {
        case SET_VISIBILITY_FILTER:
            return action.filter
        default: return state
    }
}

function todos(state = [], action) {
    switch (action.type) {
        case ADD_TODO:
            return [
                ...state,
                {text: action.text, completed: false}
            ]
        case TOGGLE_TODO:
            return state.map((todo, index) => {
                    if (index == action.index) {
                        return Object.assign({}, todo, {completed: !todo.completed})
                    }
                    return todo
                })
        default: return state
    }
}


function movies(state = {isLoading:false,subjects:[],title:'',error:''}, action) {
    switch (action.type) {
        case REQUEST_MOVIES:
            return Object.assign({},state,{
                isLoading:true,
                error:''
            });
        case REQUEST_MOVIES_SUCCESS:
            const {title,subjects} = action.data;
            return Object.assign({},state,{
                title,subjects,
                isLoading:false,
                error:''
            });
        case REQUEST_MOVIES_FAILURE:
            return Object.assign({},state,{
                error:action.error.message,
                isLoading:false
            });
        default: return state
    }
}

const todoApp = combineReducers({
    visibilityFilter,
    todos,
    movies
})

export default todoApp;


