import React, {Component} from 'react';
import {Alert, ControlLabel, FormControl, FormGroup} from "react-bootstrap";
import Button from "react-bootstrap/es/Button";
import {fetchLogin} from "./actions";
import {connect} from "react-redux";

class Login extends Component {
    constructor(props){
        super(props);

        this.login = this.login.bind(this);
        this.handleChange = this.handleChange.bind(this);

        this.state = {tel:'',pwd:''};
    }

    //监控登录状态
    componentWillReceiveProps(prevProps){
        if(prevProps.isLogin) {
            prevProps.history.goBack();
        }
    }

    render() {
        const myAlert = this.props.error ?
            (<Alert bsStyle="danger">this.props.error</Alert>) : null;

        return (
            <div>
                {myAlert}
                <form onSubmit={this.login}>
                    <FormGroup controlId="tel"
                               validationState={this.getTelValidationState()}>
                        <ControlLabel>手机号</ControlLabel>
                        <FormControl type="text" name="tel"
                            placeholder="请输入手机号"
                            onChange={this.handleChange}/>
                        <FormControl.Feedback/>
                    </FormGroup>
                    <FormGroup controlId="pwd"
                               validationState={this.getPwdValidationState()}>
                        <ControlLabel>密码</ControlLabel>
                        <FormControl type="password" name="pwd"
                            placeholder="请输入密码"
                            onChange={this.handleChange}/>
                        <FormControl.Feedback/>
                    </FormGroup>
                    <Button block type="submit">登录</Button>
                </form>
            </div>
        );
    }

    login(event) {
        event.preventDefault();

        // 校验通过才提交
        if(this.getTelValidationState()=='success' &&
            this.getPwdValidationState()=='success') {
            this.props.dispatch(fetchLogin(this.state))
        }
    }
    handleChange(event) {
        this.setState({[event.target.name]:event.target.value})
    }

    getTelValidationState() {
        if(!this.state.tel) {
            return null;
        }
        if(/1[3-8]\d{9}/.test(this.state.tel)) {
            return 'success';
        }else{
            return 'error'
        }
    }

    getPwdValidationState() {
        if(!this.state.pwd) {
            return null;
        }
        //含有大小写字母、下划线且长度为5位及以上
        if(/^[a-zA-Z\d_]{5,}$/.test(this.state.pwd)) {
            return 'success';
        }else{
            return 'error'
        }
    }
}

function select(state) {
    return {
        isLogin:state.userLogin.user ? true : false,
        error:state.userLogin.error
    }
}

export default connect(select)(Login);


