import React, {Component} from 'react';
import {Table} from "react-bootstrap";
import {fetchUserInfo} from "./actions";
import {connect} from "react-redux";
import './UserCenter.css';

class UserCenter extends Component {

    componentDidMount(){
        this.props.dispatch(fetchUserInfo());
    }

    componentWillReceiveProps(nextProps){
        if (nextProps.needLogin){
            nextProps.history.push('/login');
        }
    }

    render() {
        const {name, score, works} = this.props;
        return (
            <div className="userInfo">
                <p>
                    姓名：<span>{name}</span> &nbsp;&nbsp;
                    剩余课时：<span>{score}</span>
                </p>
                <Table bordered condensed hover responsive striped>
                    <tbody>
                    {
                        works.map(work=>(
                            <tr key={work._id}>
                                <td>{work.name}</td>
                                <td>{new Date(work.createdAt).toLocaleDateString()}</td>
                            </tr>
                        ))
                    }
                    </tbody>
                </Table>
            </div>
        );
    }
}

export default connect(state=>({
    name:state.userInfo.name,
    score:state.userInfo.score,
    works:state.userInfo.works,
    needLogin:state.userInfo.error=='Forbidden',
}))(UserCenter);


