import {
    REQUEST_LOGIN, REQUEST_LOGIN_FAILURE, REQUEST_LOGIN_SUCCESS, REQUEST_USER_INFO_FAILURE, REQUEST_USER_INFO_SUCCESS,
    REQUEST_WORKS,
    REQUEST_WORKS_FAILURE,
    REQUEST_WORKS_SUCCESS, RESET_WORKS
} from "./actions";
import {combineReducers} from "redux";

function artWorks(state={loading:false,page:1,hasMore:false,works:[],error:''}, action) {
    switch (action.type){
        case REQUEST_WORKS:
            return Object.assign({},state,{
                loading:true,
                error:''
            });
        case RESET_WORKS:
            return Object.assign({},state,{
                works: []
            });
        case REQUEST_WORKS_SUCCESS:
            const {results,currentPage,totalPages} = action.data.works;
            const works = results.length == 0 ? [] : [...state.works,...results];
            return Object.assign({},state,{
                works,
                page:currentPage,
                hasMore:(totalPages > currentPage),
                loading:false,
                error:''
            });
        case REQUEST_WORKS_FAILURE:
            return Object.assign({},state,{
                loading:false,
                error:action.error.message
            });
        default: return state;
    }
}

function userLogin(state={user:null,error:''}, action) {
    switch (action.type){
        case REQUEST_LOGIN_SUCCESS:
            return Object.assign({},state,{
                user: action.data.user,
                error:''
            });
        case REQUEST_LOGIN_FAILURE:
            return Object.assign({},state,{
                user: null,
                error:action.error.detail
            });
        default: return state;
    }
}

function userInfo(state={name:'',score:0,works:[],error:''}, action) {
    switch (action.type){
        case REQUEST_USER_INFO_SUCCESS:
            const {works,name,score} = action.data.userInfo;
            return Object.assign({},state,{
                works,name,score,
                error:''
            });
        case REQUEST_USER_INFO_FAILURE:
            return Object.assign({},state,{
                name:'',score:0,works:[],
                error:action.error.message
            });
        default: return state;
    }
}

const artWorkApp = combineReducers({artWorks,userLogin,userInfo});

export default artWorkApp;


