import React from 'react';
import {connect} from "react-redux";

function MovieApp({subjects,title}) {
    return (
        <div>
            <h2>{title}</h2>
            <div>
                {
                    subjects.map(m => (
                        <div key={m.id}>
                            <img src={m.images.small}/>
                            <h3>{m.title}</h3>
                            <p>主演：{m.casts.map(cast=>cast.name).join('，')}</p>
                            <p>评分：{m.rating.average}</p>
                        </div>
                    ))
                }
            </div>
        </div>
    );
}

function select(state) {
    return {
        subjects:state.movies.subjects,
        title:state.movies.title
    }
}

export default connect(select)(MovieApp)


