import * as React from "react";
import {Button, ButtonGroup, Image, Panel} from "react-bootstrap";
import {connect} from "react-redux";
import './PaintWorkList.css';
import {fetchArtWorks, resetWorks} from "./actions";
import InfiniteScroll from 'react-infinite-scroller';

class PaintWorkList extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            user:'all',
            time:'week',
        };

        this.onUserChange = this.onUserChange.bind(this);
        this.onTimeChange = this.onTimeChange.bind(this);
        this.loadMore = this.loadMore.bind(this);
    }
    componentDidMount(){
        this.props.dispatch(fetchArtWorks());
    }
    loadMore(){
        this.props.dispatch(fetchArtWorks({
            user:this.state.user,
            time:this.state.time,
            page:this.props.page + 1
        }));
    }
    componentWillReceiveProps(nextProps){
        if(nextProps.needLogin){
            nextProps.history.push('/login');
        }
    }
    render() {
        const {works,hasMore} = this.props;
        return (
            <div className="work-list">
                {/*过滤选项*/}
                <div className="filter">
                    作者：
                    <ButtonGroup>
                        <Button active={this.state.user=='all'}
                                onClick={()=>this.onUserChange('all')}>全部作品</Button>
                        <Button active={this.state.user=='me'}
                                onClick={()=>this.onUserChange('me')}>我的作品</Button>
                    </ButtonGroup>
                </div>
                <div className="filter">
                    时间：
                    <ButtonGroup>
                        <Button active={this.state.time=='week'}
                                onClick={()=>this.onTimeChange('week')}>本周</Button>
                        <Button active={this.state.time=='month'}
                                onClick={()=>this.onTimeChange('month')}>本月</Button>
                    </ButtonGroup>
                </div>
                {/*作品列表*/}
                <InfiniteScroll
                  loadMore={this.loadMore}
                  hasMore={hasMore}
                  initialLoad={false}
                  loader={<div className={'loader'}>加载中...</div>}>
                    {
                        works.map(work => (
                            <Panel className="work" key={work._id}>
                                <Panel.Body>
                                    <Image responsive src={work.img.url}></Image>
                                </Panel.Body>
                                <Panel.Footer>
                                    作者：{work.user.name}
                                    <span className="createdAt">
                                    {new Date(work.createdAt).toLocaleDateString()}
                                </span>
                                </Panel.Footer>
                            </Panel>
                        ))
                    }
                </InfiniteScroll>
            </div>
        );
    }

    onUserChange(user) {
        this.props.dispatch(resetWorks());
        this.props.dispatch(fetchArtWorks({user,time:this.state.time}));
        this.setState({user});
    }

    onTimeChange(time) {
        this.props.dispatch(resetWorks());
        this.props.dispatch(fetchArtWorks({user:this.state.user,time}));
        this.setState({time});
    }
}

const select = state => ({
    works: state.artWorks.works,
    page: state.artWorks.page,
    hasMore: state.artWorks.hasMore,
    needLogin: state.artWorks.error == 'Forbidden'
});
export default connect(select)(PaintWorkList);


