import EventEmitter from 'events';

export default class DataSource {

    static comments;

    static posts = [
        {id: 1, author: 'tom', content: 'react is a great tech!', pub_time: new Date()},
        {id: 2, author: 'jerry', content: 'the best tech is react!', pub_time: new Date()},
    ];

    static emitter = new EventEmitter();

    static getComments() {
        if (!this.comments) {
            //首次执行，初始化comments
            this.comments = [
                {id: 1, author: 'tom', content: 'today is a good day!', pub_time: new Date()},
                {id: 2, author: 'jerry', content: 'good day is it today!', pub_time: new Date()},
            ];
            // 并启动定时器
            setInterval(() => {
                this.comments = [
                    {id: 1, author: 'tom', content: 'today is a good day!', pub_time: new Date()},
                    {id: 2, author: 'jerry', content: 'good day is it today!', pub_time: new Date()},
                ];
                this.emitter.emit('commentChange', this.comments);
            }, 5000);
        }
        return this.comments;
    }

    static addChangeListener(callback) {
        this.emitter.addListener('commentChange', (data) => {
            //数据更新
            this.comments = data;
            //    执行回调函数
            callback();
        })
    }

    static getBlogPost(id) {
        for (let p of this.posts) {
            if(p.id == id) {
                return p;
            }
        }
        return null;
    }
}


