import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import Welcome from "./comp/Welcome";
import Clock from "./state/Clock";
import EventHandle from "./event/EventHandle";
import ToggleButton from "./event/ToggleButton";
import Greeting from "./condition/Greeting";
import LoginControl from "./condition/LoginControl";
import Mailbox from "./condition/Mailbox";
import Page from "./condition/Page";
import MyForm from "./form/MyForm";
import LiftingStateUp from "./lifting-state-up/LiftingStateUp";
import Calculator from "./lifting-state-up/Calculator";
import FilterableProductTable from "./thinking-in-react/ThinkingInReact";
import HttpRequest from "./http/HttpRequest";
import Refs from "./refs/Refs";
import {BlogPost, CommentList} from "./hoc/hoc";
import withSubscription from "./hoc/withsubscription";
import DataSource from "./hoc/DataSource";
import BaseExample from "./router/BaseExample";
import EmbedRouteExample from "./router/EmbedRouteExample";
import RouteGuardExample from "./router/RouteGuardExample";
import TodoApp from "./redux/TodoApp";
import {Provider} from "react-redux";
import {applyMiddleware, createStore} from "redux";
import reduxThunk from "redux-thunk";
import todoApp from "./redux/reducers";
import MovieApp from "./redux/MovieApp";
import {fetchMovies} from "./redux/movie-actions";
import artWorkApp from "./art-works/reducers";
import {fetchArtWorks} from "./art-works/actions";
import PaintWorkList from "./art-works/PaintWorkList";
import {BrowserRouter, Route} from "react-router-dom";
import Login from "./art-works/Login";
import {Nav, Navbar, NavItem} from "react-bootstrap";
import UserCenter from "./art-works/UserCenter";

const PRODUCTS = [
    {category: 'Sporting Goods', price: '$49.99', stocked: true, name: 'Football'},
    {category: 'Sporting Goods', price: '$9.99', stocked: true, name: 'Baseball'},
    {category: 'Sporting Goods', price: '$29.99', stocked: false, name: 'Basketball'},
    {category: 'Electronics', price: '$99.99', stocked: true, name: 'iPod Touch'},
    {category: 'Electronics', price: '$399.99', stocked: false, name: 'iPhone 5'},
    {category: 'Electronics', price: '$199.99', stocked: true, name: 'Nexus 7'}
];


//创建store
// const store = createStore(todoApp,applyMiddleware(reduxThunk));
const store = createStore(artWorkApp,applyMiddleware(reduxThunk));

class App extends Component {
    isLoggedIn = false;

    render() {
        // const CommentListWithSubscription = withSubscription(
        //     CommentList,(DataSource)=>DataSource.getComments());
        // const BlogPostWithSubscription = withSubscription(
        //     BlogPost,(DataSource,props)=>DataSource.getBlogPost(props.id));

        return (
            <div className="App">
                <Provider store={store}>
                    {/*<TodoApp/>*/}
                    {/*<MovieApp/>*/}
                    <BrowserRouter>
                        <div>
                            <Navbar>
                                <Navbar.Header>
                                    <Navbar.Brand>
                                        <a href="/">SIMS</a>
                                    </Navbar.Brand>
                                    <Navbar.Toggle></Navbar.Toggle>
                                </Navbar.Header>
                                <Navbar.Collapse>
                                    <Nav>
                                        <NavItem href="/">作品</NavItem>
                                        <NavItem href="/user">个人中心</NavItem>
                                    </Nav>
                                </Navbar.Collapse>
                            </Navbar>
                            <div>
                                <Route exact path="/" component={PaintWorkList}/>
                                <Route path="/login" component={Login}/>
                                <Route path="/user" component={UserCenter}/>
                            </div>
                        </div>
                    </BrowserRouter>

                </Provider>
                {/*<RouteGuardExample/>*/}
                {/*<EmbedRouteExample/>*/}
                {/*<BaseExample/>*/}
                {/*<BlogPostWithSubscription id={1}/>*/}
                {/*<CommentListWithSubscription/>*/}
                {/*<Refs/>*/}
                {/*<HttpRequest/>*/}
                {/*<FilterableProductTable products={PRODUCTS} />*/}
                {/*<Calculator/>*/}
                {/*<LiftingStateUp/>*/}
                {/*<MyForm/>*/}
                {/*<Page/>*/}
                {/*<Mailbox unreadMessages={['React', 'React Native']}></Mailbox>*/}
                {/*<LoginControl/>*/}
                {/*<ToggleButton/>*/}
                {/*<EventHandle/>*/}
                {/*<Welcome name="Tom"/>*/}
                {/*<Welcome name="Jerry"/>*/}
                {/*<Welcome name="Mike"/>*/}
                {/*<Clock/>*/}
            </div>
        );
    }
}

export default App;



