import React, {Component} from 'react';
import axios from 'axios';

class HttpRequest extends Component {
    constructor(props) {
        super(props);
        this.state = {users: [],user: {name: '', age: '', id: ''}};
    }

    componentDidMount() {
        axios.get('/api/works')
            .then(response => {
                console.log(response);
                this.setState({users: response.data});
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    render() {
        return (
            <div>
                <UserList users={this.state.users}
                    onSelectUser={user=>this.setState({user})}/>
                <UserForm user={this.state.user}
                          onSubmitUser={user=>this.submitUser(user)}/>
            </div>
        );
    }

    submitUser(user) {
        axios.post('/users', user)
            .then((response) => {
                if(response.data.success) {
                    alert('更新成功！');
                    this.setState({users: response.data.users, user});
                }
            })
            .catch((error) => {
                console.log(error);
            });
    }
}

class UserList extends Component {
    render() {
        const userList = this.props.users.map(
            user => <li key={user.id}
                        onClick={() => this.selectUser(user)}>
                {user.name}
            </li>);
        return (
            <ul>{userList}</ul>
        );
    }

    selectUser(user) {
        console.log(user);
        this.props.onSelectUser(user);
    }
}

class UserForm extends Component {

    constructor(props) {
        super(props);
        this.state = {name: '', age: '', id: ''};
        this.onSubmit = this.onSubmit.bind(this);
        this.onValueChange = this.onValueChange.bind(this);
    }

    componentWillReceiveProps(nextProps){
        console.log('componentWillReceiveProps');
        this.setState(nextProps.user);
    }

    render() {
        return (
            <form onSubmit={this.onSubmit}>
                <p><input type="text" value={this.state.id} disabled/></p>
                <p><input type="text" value={this.state.name} name={'name'}
                          onChange={this.onValueChange}/></p>
                <p><input type="text" value={this.state.age} name={'age'}
                          onChange={this.onValueChange}/></p>
                <input type="submit" value={'提交'}/>
            </form>
        );
    }

    onSubmit(e) {
        e.preventDefault();
        this.props.onSubmitUser(this.state);
    }

    onValueChange(e) {
        this.setState({[e.target.name]:e.target.value});
    }
}

export default HttpRequest;


