import React,{Component} from "react";
import logo from '../logo.svg';
import './Jsx2.css';

//传递常量属性
const ele1 = <div tabIndex="0">传递常量属性</div>;
const user = {avatar:logo};
const ele2 = <img src={user.avatar} className="avatar"/>;

export default class Jsx2 extends Component{
    render(){
        return ele2;
    }
}


