import React,{Component} from "react";

const user = {
    firstName:'Tom',
    lastName:'Cruse'
};

//jsx指定给常量
const jsx = <h1>Hello,Stranger.</h1>;

function formatName(user) {
    return user.firstName + ' ' + user.lastName;
}

function getGreeting(user) {
    //jsx可以写在if状态中
    if(user) {
        return <h1>Hello,{formatName(user)}!</h1>;
    }
    return jsx;
}

export default class Jsx1 extends Component{
    render(){
        //jsx中嵌入表达式
        // return <h1>Hello,{formatName(user)}!</h1>;
        // return <h1>Hello,{user.firstName+' '+user.lastName}!</h1>;
        // return <h1>Hello,{'Tom' +' '+'Cruse'}!</h1>;
        return getGreeting();
    }
}


