import React, {Component} from 'react';

class EventHandle extends Component {
    render() {
        return (
            <div>
                <button onClick={this.clickHandler}>
                    Click Me!
                </button>
                <a href="#" onClick={this.handleClick}>
                    PreventDefault
                </a>
                <button onClick={this.deleteRow.bind(this, 1)}>Delete Row</button>
            </div>
        );
    }

    clickHandler() {
        alert('oh!click!!');
    }

    handleClick(e) {
        e.preventDefault();
        alert('oh!PreventDefault!!');
    }
    deleteRow(id,e){
        console.log(this);
        alert('delete row'+id);
    }
}

export default EventHandle;


