import fetch from 'isomorphic-fetch';

export const REQUEST_WORKS = 'REQUEST_WORKS';
export const RESET_WORKS = 'RESET_WORKS';
export const REQUEST_WORKS_SUCCESS = 'REQUEST_WORKS_SUCCESS';
export const REQUEST_WORKS_FAILURE = 'REQUEST_WORKS_FAILURE';

function requestWorks() {
    return {type: REQUEST_WORKS}
}

export function resetWorks() {
    return {type: RESET_WORKS}
}

function requestWorksSuccess(data) {
    return {type: REQUEST_WORKS_SUCCESS, data}
}

function requestWorksFailure(error) {
    return {type: REQUEST_WORKS_FAILURE, error}
}

// 异步动作
export function fetchArtWorks(params = {user: 'all', time: 'week', page: 1}) {
    return function (dispatch) {
        dispatch(requestWorks());

        return fetch(
            `/api/works?user=${params.user}&time=${params.time}&page=${params.page}`,
            {credentials: 'include'}
        )
            .then(resp => {
                    if (resp.status >= 200 && resp.status < 300) {
                        return resp.json()
                    } else {
                        const err = new Error(resp.statusText);
                        err.response = resp;
                        throw err;
                    }
                }
            )
            .then(data => {console.log(data);dispatch(requestWorksSuccess(data))})
            .catch(err => dispatch(requestWorksFailure(err)))
    }
}

//登录动作定义
export const REQUEST_LOGIN = 'REQUEST_LOGIN';
export const REQUEST_LOGIN_SUCCESS = 'REQUEST_LOGIN_SUCCESS';
export const REQUEST_LOGIN_FAILURE = 'REQUEST_LOGIN_FAILURE';

function requestLogin() {
    return {type: REQUEST_LOGIN}
}

function requestLoginSuccess(data) {
    return {type: REQUEST_LOGIN_SUCCESS, data}
}

function requestLoginFailure(error) {
    return {type: REQUEST_LOGIN_FAILURE, error}
}

// 异步动作
export function fetchLogin(params) {
    return function (dispatch) {
        dispatch(requestLogin());

        return fetch(`/api/login`, {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(params),
            credentials: 'include'
        })
            .then(
                resp => resp.json(),
                error => {
                    throw error
                }
            )
            .then(
                data => {
                    dispatch(requestLoginSuccess(data))
                }
            )
            .catch(err => {
                dispatch(requestLoginFailure(err))
            })
    }
}

export const REQUEST_USER_INFO_SUCCESS = 'REQUEST_USER_INFO_SUCCESS';
export const REQUEST_USER_INFO_FAILURE = 'REQUEST_USER_INFO_FAILURE';

function requestUserInfoSuccess(data){
    return {type:REQUEST_USER_INFO_SUCCESS,data}
}

function requestUserInfoFailure(error){
    return {type:REQUEST_USER_INFO_SUCCESS,error}
}

export function fetchUserInfo() {
    return function (dispatch) {
        return fetch(
            `/api/userInfo`,
            {credentials: 'include'}
        )
            .then(resp => {
                    if (resp.status >= 200 && resp.status < 300) {
                        return resp.json()
                    } else {
                        const err = new Error(resp.statusText);
                        err.response = resp;
                        throw err;
                    }
                }
            )
            .then(data => dispatch(requestUserInfoSuccess(data)))
            .catch(err => dispatch(requestUserInfoFailure(err)))
    }
}


