import React, {Component} from 'react';
import {createStore} from "redux";
import {connect, Provider} from 'react-redux'
import todoApp from "./reducers";
import {addTodo, setVisibility, toggleTodo, Visibility_Filters} from "./actions";

class TodoApp extends Component {
    render() {
        const {dispatch, todos, visibilityFilter} = this.props;
        return (

            <div>
                <p>
                    <input type="radio" name={'filter'}
                           value={Visibility_Filters.SHOW_ALL}
                           checked={Visibility_Filters.SHOW_ALL == visibilityFilter}
                           onChange={(evt) => dispatch(setVisibility(evt.target.value))}/>全部
                    <input type="radio" name={'filter'}
                           value={Visibility_Filters.SHOW_ACTIVE}
                           checked={Visibility_Filters.SHOW_ACTIVE == visibilityFilter}
                           onChange={(evt) => dispatch(setVisibility(evt.target.value))}/>进行中
                    <input type="radio" name={'filter'}
                           value={Visibility_Filters.SHOW_COMPLETED}
                           checked={Visibility_Filters.SHOW_COMPLETED == visibilityFilter}
                           onChange={(evt) => dispatch(setVisibility(evt.target.value))}/>已完成
                </p>
                <ul>
                    {
                        todos.map((todo, index) =>
                            <li key={index}
                                onClick={() => dispatch(toggleTodo(index))}>
                                {todo.text} - {todo.completed ? '已完成' : '进行中'}
                            </li>)
                    }
                </ul>
                <p>
                    <input type="text" ref={(elem) => this.elem = elem}/>
                    <button onClick={() => dispatch(addTodo(this.elem.value))}>新增待办</button>
                </p>
            </div>

        );
    }
}

function selectTodos(todos, visibilityFilter) {
    //状态变更
    return todos.filter(todo => {
        if (visibilityFilter == Visibility_Filters.SHOW_COMPLETED) {
            return todo.completed
        } else if (visibilityFilter == Visibility_Filters.SHOW_ACTIVE) {
            return !todo.completed
        } else {
            return true
        }
    });
}

function select(state) {
    return {
        todos: selectTodos(state.todos, state.visibilityFilter),
        visibilityFilter: state.visibilityFilter
    }
}

export default connect(select)(TodoApp);


