import fetch from 'isomorphic-fetch';

//请求热映电影动作
export const REQUEST_MOVIES = 'REQUEST_MOVIES';

function requestMovies() {
    return {
        type: REQUEST_MOVIES
    }
}

//请求热映电影成功
export const REQUEST_MOVIES_SUCCESS = 'REQUEST_MOVIES_SUCCESS';

function requestMoviesSuccess(data) {
    return {
        type: REQUEST_MOVIES_SUCCESS,
        data
    }
}

//请求热映电影失败
export const REQUEST_MOVIES_FAILURE = 'REQUEST_MOVIES_FAILURE';

function requestMoviesFailure(error) {
    return {
        type: REQUEST_MOVIES_FAILURE,
        error
    }
}

//异步请求动作
export function fetchMovies() {
    return function (dispatch) {
        //发起api请求
        dispatch(requestMovies());

        //请求
        return fetch('/v2/movie/in_theaters')
            .then(
                resp => resp.json(),
                err => {
                    console.log(err);
                    dispatch(requestMoviesFailure(err));
                }
            )
            .then(json => {
                dispatch(requestMoviesSuccess(json))
            })
    }
}



